import React, { useEffect, useState, useMemo } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Spinner from "react-bootstrap/Spinner";
import Table from "react-bootstrap/Table";
import Modal from "react-bootstrap/Modal";
import { useAuth } from "../contexts/AuthContext";
import { toast } from "react-toastify";

const DEVICE_FIELDS = [
  { key: "id", label: "ID" },
  { key: "name", label: "Name" },
  { key: "siteId", label: "Site ID" },
  { key: "ipAddress", label: "IP Address" },
  { key: "uid", label: "UID" },
  { key: "type", label: "Type" },
  { key: "manufacturer", label: "Manufacturer" },
  { key: "model", label: "Model" },
  { key: "operatingSystem", label: "Operating System" },
  { key: "active", label: "Active" },
  { key: "deviceConfigurationId", label: "Device Config ID" },
  { key: "availablePorts", label: "Available Ports" },
  { key: "userAgentId", label: "User Agent ID" },
];

function compare(a, b, key, direction) {
  if (a[key] == null && b[key] == null) return 0;
  if (a[key] == null) return 1;
  if (b[key] == null) return -1;
  if (typeof a[key] === "string") {
    return direction === "asc"
      ? a[key].localeCompare(b[key])
      : b[key].localeCompare(a[key]);
  }
  return direction === "asc" ? a[key] - b[key] : b[key] - a[key];
}

const DeviceCard = ({ device, onSave }) => {
  const [show, setShow] = useState(false);
  return (
    <>
      <Card
        className="mb-3 shadow-sm device-card-mini text-truncate"
        style={{ cursor: "pointer", minWidth: 0, maxWidth: 260, width: "100%" }}
        onClick={() => setShow(true)}
      >
        <Card.Body className="p-2 d-flex flex-column align-items-start justify-content-between">
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>ID:</strong> {device.id}</div>
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>Name:</strong> {device.name}</div>
          <div className="mb-2 text-truncate" style={{ width: "100%", fontSize: "0.85em", color: "#888" }}><strong>UID:</strong> {device.uid}</div>
          <Button variant="outline-primary" size="sm" className="align-self-end" onClick={e => { e.stopPropagation(); setShow(true); }}>Details</Button>
        </Card.Body>
      </Card>
      <DeviceDetailsModal show={show} onHide={() => setShow(false)} device={device} onSave={onSave} />
    </>
  );
};


function DeviceDetailsModal({ show, onHide, device, onSave, mode = "edit" }) {
  const [form, setForm] = useState(() => {
    if (mode === "add") {
      return { ...device, id: 0 };
    }
    return device || {};
  });
  const [saving, setSaving] = useState(false);
  const { authenticatedFetch } = useAuth();

  useEffect(() => {
    if (mode === "add") {
      setForm({ ...device, id: 0 });
    } else {
      setForm(device || {});
    }
  }, [device, show, mode]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((f) => ({
      ...f,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSave = async () => {
    setSaving(true);
    try {
      const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
      let resp, updated;
      if (mode === "add") {
        resp = await authenticatedFetch(`${baseUrl}/api/devices`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(form),
        });
        if (!resp.ok) throw new Error("Failed to add device");
        updated = await resp.json();
        toast.success("Device added");
      } else {
        resp = await authenticatedFetch(`${baseUrl}/api/devices`, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(form),
        });
        if (!resp.ok) throw new Error("Failed to save device");
        updated = await resp.json();
        toast.success("Device updated");
      }
      onSave(updated);
      onHide();
    } catch (e) {
      toast.error(e.message || (mode === "add" ? "Error adding device" : "Error saving device"));
    } finally {
      setSaving(false);
    }
  };

  return (
    <Modal show={show} onHide={onHide} size="lg" centered contentClassName="device-modal-responsive">
      <Modal.Header closeButton>
        <Modal.Title>{mode === "add" ? "Add New Device" : "Device Details"}</Modal.Title>
      </Modal.Header>
      <Modal.Body style={{ maxHeight: '60vh', overflowY: 'auto', minHeight: 200 }}>
        <Form>
          {DEVICE_FIELDS.map(({ key, label }) => (
            <Form.Group key={key} className="mb-3" as={Row}>
              <Form.Label column sm={4} xs={5} style={{ fontWeight: 500 }}>{label}</Form.Label>
              <Col sm={8} xs={7}>
                {key === "active" ? (
                  <Form.Check
                    type="checkbox"
                    name={key}
                    checked={!!form[key]}
                    onChange={handleChange}
                    disabled={saving}
                  />
                ) : (
                  <Form.Control
                    name={key}
                    value={form[key] ?? ""}
                    onChange={handleChange}
                    disabled={saving || key === "id"}
                    readOnly={key === "id"}
                    type={typeof form[key] === "number" ? "number" : "text"}
                  />
                )}
              </Col>
            </Form.Group>
          ))}
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={handleSave} disabled={saving}>
          {saving ? <Spinner size="sm" animation="border" /> : mode === "add" ? "Add" : "Save"}
        </Button>
        <Button variant="secondary" onClick={onHide} disabled={saving}>Cancel</Button>
      </Modal.Footer>
    </Modal>
  );
}

const DevicesScreen = () => {
  const { user, authenticatedFetch } = useAuth();
  const [devices, setDevices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState(DEVICE_FIELDS.reduce((acc, f) => ({ ...acc, [f.key]: "" }), {}));
  const [showAdvanced, setShowAdvanced] = useState(false);
  const [sort, setSort] = useState({ key: "id", direction: "asc" });

  useEffect(() => {
    setLoading(true);
    const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
    authenticatedFetch(`${baseUrl}/api/devices`)
      .then((r) => {
        if (!r.ok) throw new Error("Failed to fetch devices");
        return r.json();
      })
      .then((data) => setDevices(data))
      .catch((e) => toast.error(e.message || "Error loading devices"))
      .finally(() => setLoading(false));
  }, [authenticatedFetch]);

  const filtered = useMemo(() => {
    return devices.filter((d) =>
      DEVICE_FIELDS.every(({ key }) => {
        if (!filter[key]) return true;
        return d[key]?.toString().toLowerCase().includes(filter[key].toLowerCase());
      })
    );
  }, [devices, filter]);

  const sorted = useMemo(() => {
    return [...filtered].sort((a, b) => compare(a, b, sort.key, sort.direction));
  }, [filtered, sort]);

  const [showAdd, setShowAdd] = useState(false);
  const handleSave = (updated) => {
    setDevices((ds) => {
      // If device already exists, update it; otherwise, add to front
      const idx = ds.findIndex((d) => d.id === updated.id);
      if (idx !== -1) {
        return ds.map((d) => (d.id === updated.id ? updated : d));
      } else {
        return [updated, ...ds];
      }
    });
  };

  if (!user || !(user.roles?.includes("admin") || user.roles?.includes("device"))) {
    return <div className="mt-4 text-danger">Access denied.</div>;
  }

  return (
    <div className="container mt-4">
      <div className="d-flex justify-content-between align-items-center mb-2">
        <h2 className="mb-0">Devices</h2>
        <Button variant="success" size="sm" onClick={() => setShowAdd(true)}>
          + Add new device
        </Button>
      </div>
      <DeviceDetailsModal
        show={showAdd}
        onHide={() => setShowAdd(false)}
        device={{}}
        onSave={handleSave}
        mode="add"
      />
      <Table bordered size="sm" className="mb-4">
        <thead>
          <tr>
            {(showAdvanced
              ? DEVICE_FIELDS
              : DEVICE_FIELDS.slice(0, 3)
            ).map(({ key, label }) => (
              <th key={key} style={{ cursor: "pointer" }} onClick={() => setSort((s) => ({
                key,
                direction: s.key === key && s.direction === "asc" ? "desc" : "asc"
              }))}>
                {label} {sort.key === key ? (sort.direction === "asc" ? "▲" : "▼") : null}
              </th>
            ))}
          </tr>
          <tr>
            {(showAdvanced
              ? DEVICE_FIELDS
              : DEVICE_FIELDS.slice(0, 3)
            ).map(({ key }) => (
              <th key={key}>
                <Form.Control
                  size="sm"
                  placeholder="Filter"
                  value={filter[key]}
                  onChange={(e) => setFilter((f) => ({ ...f, [key]: e.target.value }))}
                />
              </th>
            ))}
          </tr>
          <tr>
            <th colSpan={DEVICE_FIELDS.length} className="p-1 text-end">
              <Button
                variant="outline-secondary"
                size="sm"
                onClick={() => setShowAdvanced((v) => !v)}
                aria-expanded={showAdvanced}
              >
                {showAdvanced ? "Hide advanced filters" : "Show advanced filters"}
              </Button>
            </th>
          </tr>
        </thead>
      </Table>
      {loading ? (
        <div className="text-center my-5">
          <Spinner animation="border" />
        </div>
      ) : (
        <Row className="g-2">
          {sorted.map((device) => (
            <Col key={device.id} xs={12} sm={6} md={4} lg={3} xl={2} xxl={2}>
              <DeviceCard device={device} onSave={handleSave} />
            </Col>
          ))}
        </Row>
      )}
      {sorted.length === 0 && !loading && <div>No devices found.</div>}
    </div>
  );
};

export default DevicesScreen;
